<template>
  <div class="products">
    <div class="container">
        <hr>
        <div class="product-test">
          <h3 class="d-inline-block">Lista de produtos</h3>
          <button @click="addNew" class="btn btn-primary btn-sm float-right">Novo produto</button>

          <div class="table-responsive">
              <table class="table">
                <thead>
                  <tr>
                    <th>Descrio</th>
                    <th>Stock</th>
                    <th>Preo</th>
                    <th class="text-center"><i class="fas fa-cogs"></i></th>
                  </tr>
                </thead>

                <tbody>
                  <tr v-for="(product, index) in products" :key="index">
                    <td>{{ product.name }}</td>
                    <td>{{ product.stock }}</td>
                    <td>{{ product.price }}</td>
                    <td class="text-center">
                      <button @click="editProduct(product)" class="btn btn-sm btn-primary mr-2">
                        <i class="fas fa-edit"></i>
                      </button>
                      <button @click="deleteProduct(product)" class="btn btn-sm btn-danger ml-2">
                        <i class="fas fa-trash"></i>
                      </button>
                    </td>
                  </tr>
                </tbody>
              </table>
          </div>
        </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" id="product" tabindex="-1" role="dialog" aria-labelledby="editLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="editLabel">Editar produto</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">

              <div class="row">
                <!-- main product -->
                <div class="col-md-8">
                  <div class="form-group">
                    <input type="text" placeholder="Nome do produto" v-model="product.name" class="form-control">
                  </div>

                  <div class="form-group">
                    <input type="number" step="any" min="0" placeholder="Stock" v-model="product.stock" class="form-control">
                  </div>

                  <div class="form-group">
                    <vue-editor v-model="product.description"></vue-editor>
                  </div>
                </div>
                <!-- product sidebar -->
                <div class="col-md-4">
                  <h5 class="display-6">Detalhes do produto</h5>
                  <hr>

                  <div class="form-group">
                    <input type="text" placeholder="Preo" v-model="product.price" class="form-control">
                  </div>

                  <div class="form-group">
                    <input @keyup.188="addTag()" type="text" placeholder="Tags do produto" v-model="tag" class="form-control">
                    <div class="d-flex">
                      <p v-for="(tag, index) in product.tags" :key="index">
                        {{ tag }}
                      </p>
                    </div>
                  </div>

                  <div class="form-group">
                    <label for="product_image">Imagens do produto</label>
                    <input type="file" @change="uploadImage" class="form-control">
                  </div>

                  <div class="d-flex">
                    <div class="p-1" v-for="(img, index) of product.image" :key="index">
                      <div class="img-wrapp">
                        <img :src="img" width="80px" alt="">
                        <span @click="deleteImage(img, index)" class="delete-img">X</span>
                      </div>
                    </div>
                  </div>

                </div>
              </div>




          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancelar</button>
            <button v-if="modal == 'new'" @click="addProduct()" type="button" class="btn btn-primary">Gravar</button>
            <button v-if="modal == 'edit'" @click="updateProduct()" type="button" class="btn btn-success">Actualizar</button>
          </div>
        </div>
      </div>
    </div>


    
  </div>
</template>

<script>
import { VueEditor } from 'vue2-editor';
import { fb, db} from '../firebase';

export default {
  name: "Products",
  components: {
    VueEditor
  },
  data(){
    return {
        products: [],
        product: {
          name: null,
          description: null,
          stock: null,
          price: null,
          tags: [],
          image: []
        },
        activeItem: null,
        modal: null,
        tag: null,
    }
  },
  firestore(){
      return {
        products: db.collection('products'),
      }
  },
  methods:{
    deleteImage(img, index) {
      let image = fb.storage().refFromURL(img);

      this.product.image.splice(index, 1);

      image.delete().then(() => {
        console.log('Image deleted');
      })
      .catch((error) => {
        console.log('An error occurred');
      })
    },
    uploadImage(e) {

      if(e.target.files[0]) {
        let file = e.target.files[0];
        var storageRef = fb.storage().ref('products/' + file.name);
        let uploadTask = storageRef.put(file);

        uploadTask.on('state_changed', (snapshot) => {
          
        }, (error) => {
          // Handle unsuccessful uploads
        }, () => {
          // Handle successful uploads on complete
          // For instance, get the download URL: https://firebasestorage.googleapis.com/...
          uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
            this.product.image.push(downloadURL);
            console.log('File available at', downloadURL);
            
          });
        });
      }
      
    },
    addTag() {
      this.product.tags.push(this.tag);
      this.tag = '';
    },
    reset() {
      this.product = {
        name: null,
        description: null,
        stock: null,
        price: null,
        tags: [],
        image: []
      }
    },
    addNew(){
      this.modal = 'new';
      this.reset();
      $('#product').modal('show');
    },
    updateProduct(){
      this.$firestore.products.doc(this.product.id).update(this.product);
      Toast.fire({
          type: 'success',
          title: 'Actualizado com sucesso'
        })
        $('#product').modal('hide');
    },
    editProduct(product) {
      this.modal = 'edit';
      $('#product').modal('show');
      this.product = product;
    },
    deleteProduct(doc) {
        Swal.fire({
        title: 'Excluir este produto?',
        text: "No vai poder recuperar esse dado!",
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Sim, apagar!'
      }).then((result) => {
        if (result.value) {
          
          this.$firestore.products.doc(doc.id).delete();
          Toast.fire({
            type: 'success',
            title: 'Excludo com sucesso'
          });
          $('#product').modal('hide');
        }
      })
    },
    readData(){
      
     
    },
    addProduct() {
      this.$firestore.products.add(this.product);
      $('#product').modal('hide');
    }
  
  },
  created(){
  
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.img-wrapp {
  position: relative;
}
.img-wrapp span.delete-img {
  position: absolute;
  top: -14px;
  left: -2px;
}
.img-wrapp span.delete-img:hover {
  cursor: pointer;
}
</style>