<template>
    <div class="products" id="Product-list">
        <!-- <h3 class="text-center p-2">Lista de produtos</h3> -->
        <div class="row">             
            <div class="col-md-3 text-center" v-for="(product, index) in products" :key="index">
                <ProductCart :carrinha="product" />
            </div>
        </div>   
    </div>
</template>

<script>
import { db } from '../firebase';
import ProductCart from '../components/ProductCart.vue'; 

export default {
    name: "Product-list",
    components: {
        ProductCart,
    },
    data(){
        return {
            products: [],
        }
    },
    firestore(){
        return {
            products: db.collection('products'),
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .products{
        margin-top: 2rem;
        background: #f2f2f2;
        padding-bottom: 2rem;
    }
</style>