<template>
    <div class="product-cart">
        <div class="card">
        <img :src="carrinha.image[0]" class="card-img-top" alt="...">
            <div class="card-body text-center">
                <h5 class="card-title">{{ carrinha.price }}</h5>
                <p class="card-text" v-html="carrinha.name">
                    <!-- {{ carrinha.description }} -->
                </p>
                <button @click="addToCart()" href="#" class="btn btn-sm btn-info ml-auto">
                    <i class="fas fa-shopping-cart"></i>
                    <sup><i class="fas fa-plus"></i></sup>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import { db } from '../firebase';
export default {
    name: 'product-cart',
    props: ['carrinha'],
    data() {
        return {
            order: [],
        }
    },
    firestore(){
      return {
        orders: db.collection('orders'),
      }
  },
  methods: {
      addToCart() {
          this.$firestore.orders.add(this.carrinha);
      }
  }
}
</script>

